import React from 'react';
import { Link } from 'react-router-dom';
import classes from './Logo.module.scss';

const Logo = () => {
    return ( 
        <div className={classes.header_logo}>
            <Link to="/"><img className={classes.logo} src={require("../../../images/logo.png")} alt="" /></Link>
        </div>
     );
}
 
export default Logo;