import React, { useState } from 'react';
import { NavLink, Link } from 'react-router-dom'
import classes from './MobMenu.module.scss';

const MobMenu = () => {
    const [isActive, setisActive] = useState(
      {isActive: false}
      )

    const handleClick = () => {
      setisActive(!isActive);
    }

    return ( 
      <div>
        {/* BURGER ICON */}
        <div className={classes.wrapper}>
          <div className={classes.icon} onClick={handleClick}>
            <Link to="#" className={`${classes.menu_trigger} ${isActive ? '' : classes.active}`}>
              <span></span>
              <span></span>
              <span></span>
            </Link>
          </div>
        </div>
        {/* OVERLAY */}
        <div className={`${classes.overlay} ${isActive ? '' : classes.open}`}>
          <nav className={classes.overlay_menu}>
            <ul onClick={handleClick}>
              <li><NavLink to='/' activeClassName={classes.active} exact>Naslovna</NavLink></li>
              <li><NavLink to="/services">Usluge</NavLink></li>
              <li><NavLink to="/doktori" activeClassName={classes.active}> Doktori</NavLink></li>
              <li><NavLink to="/vesti" activeClassName={classes.active}>Vesti</NavLink></li>
              <li><NavLink to="/about">O nama</NavLink></li>
              <li><NavLink to="/kontakt">Kontakt</NavLink></li>
            </ul>
          </nav>
        </div>
      </div>
    );
}
 
export default MobMenu;