import React, { useState, useEffect } from 'react';
import classes from './Search.module.scss';

const Search = () => {

  const [width, setWidth] = useState(window.innerWidth);
  
    useEffect(() => {
        const handleResize = () => setWidth(window.innerWidth);
        window.addEventListener('resize', handleResize);
        return () => {
          window.removeEventListener('resize', handleResize);
        };
      }, []);

    const [isActive, setisActive] = useState(
        {isActive: false}
        )
  
      const handleClick = () => {
        setisActive(!isActive);
        console.log('clicked');
      }
    return ( 
        <div>
        {width < 992 ? (
        <div onClick={handleClick} className={classes.wrapper}>
            <div className={classes.search}>
                <img src={require("../../../images/search.svg")} alt="call phone"/>
            </div>
        </div>
        ) : (
          <button onClick={handleClick} className={classes.searchButton}>
            <i className="fa fa-search"></i>
          </button>
        )}
        {/* OVERLAY */}
        <div className={`${classes.overlay} ${isActive ? '' : classes.open}`}>
          <nav className={classes.overlay_menu}>
          <div className={classes.search}>
                    <input type="text" className={classes.searchTerm} placeholder="Kako vam možemo pomoći?" />
                    <button type="submit" className={classes.searchButton}>
                        <i className="fa fa-search"></i>
                    </button>
                </div> 
          </nav>
        </div>

        </div>
     );
}
 
export default Search;