import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Nav from './components/Nav/Nav';
import Main from './components/Main/Main';
import Doctors from './components/Doctors/Doctors';
import News from './components/News/News';
import Loader from './components/Loader/Loader';
import DataContextProvider from './contexts/DataContext';
import Services from './components/Services/Services';
import Contact from './components/Contact/Contact';
import About from './components/About/About';
import headerSlider from './components/Main/HeaderSlider';
import Ana from './components/Doctors/Ana/Ana';
import Hirurgija from './components/Services/Single/Hirurgija';

function App() {
  return (
    
    <div className="App">
    <DataContextProvider>
      <Loader />
    <BrowserRouter>
      <Nav />
          <Switch>
            <Route exact path='/' component={headerSlider} /> 
            <Route exact path='/services' component={Services} />  
            <Route exact path='/doktori' component={Doctors} />  
            <Route path='/vesti' component={News} />
            <Route path='/kontakt' component={Contact} />  
            <Route path='/about' component={About} />
            <Route path="/doktori/anamalivukovic" component={Ana} />
            <Route path='/services/hirurgija' component={Hirurgija} /> 
          </Switch>
      </BrowserRouter>
      </DataContextProvider>
    </div>
    
    
  );
}

export default App;
